import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';

@Injectable({
  providedIn: 'root'
})

export class SendEmailService {

  constructor(private http: HttpClient) { }

  //call to server to send the email
  sendEmail(data): Observable<any> {
    //for lochalhost
    //let baseUrl = 'http://localhost:3000/api/';

    //for the server in ip: 213.8.208.227
    let baseUrl = 'http://213.8.208.227:3000/api/';

    //api name of the function to send email
    let apiName = "send-email";

    let httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };

    return this.http.post(baseUrl + apiName, JSON.stringify(data), httpOptions);
  }

}
