import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AviAmsalemHelpingComponent } from './components/avi-amsalem-helping/components/avi-amsalem-helping/avi-amsalem-helping.component';
import { ComingSoonComponent } from './components/coming-soon-page/coming-soon.component';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { HomePageComponent } from './components/home-page/home-page.component';
import { ContactComponent } from './components/contact/contact.component';
import { GalleryComponent } from './components/gallery/gallery.component';
import { DonationComponent } from './components/donation/donation.component';


const routes: Routes = [
  {
    path: "",
    component: HomePageComponent
  }, {
    path: "about-us",
    component: AboutUsComponent
  }, {
    path: "gallery",
    component: GalleryComponent
  }, {
    path: "contact",
    component: ContactComponent
  }, {
    path: "donation",
    component: DonationComponent
  }, {
    path: "helping",
    component: AviAmsalemHelpingComponent
  }];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }
