import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SendEmailService } from 'src/app/services/send-email.service';
import { HttpErrorResponse } from '@angular/common/http';

declare var $: any;

@Component({
  selector: 'app-avi-amsalem-helping-modal',
  templateUrl: './avi-amsalem-helping-modal.component.html',
  styleUrls: ['./avi-amsalem-helping-modal.component.css']
})
export class AviAmsalemHelpingModalComponent implements OnInit {

  @Input() dataForModal: any;
  @Output() onCloseModal = new EventEmitter();

  public data = {
    fieldOf: null,
    name: "",
    address: "",
    phone: null,
    email: "",
    message: "",
    file: []
  };
  public responseMessage = {};
  public sending: boolean = false;
  public emailInvalid: boolean = true;
  public phoneInvalid: boolean = true;

  constructor(private sendEmailService: SendEmailService) { }

  ngOnInit() {
    this.data = {
      fieldOf: this.dataForModal,
      name: "",
      address: "",
      phone: null,
      email: "",
      message: "",
      file: []
    }
    $('#myModal').modal('show');
  }

  validatePhone() {
    if (/^\d*$/.test(this.data['phone'])) {
      this.phoneInvalid = true;

      $(document).ready(function () {
        var element = document.getElementById("phone");
        element.classList.remove("ng-invalid");
        element.classList.add("ng-valid");
      });
    }
    else {
      this.phoneInvalid = false;

      $(document).ready(function () {
        var element = document.getElementById("phone");
        element.classList.remove("ng-valid");
        element.classList.add("ng-invalid");
      });
    }
  }


  validateEmail() {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.data['email'])) {
      this.emailInvalid = true;

      $(document).ready(function () {
        var element = document.getElementById("email");
        element.classList.remove("ng-invalid");
        element.classList.add("ng-valid");
      });
    }
    else {
      this.emailInvalid = false;

      $(document).ready(function () {
        var element = document.getElementById("email");
        element.classList.remove("ng-valid");
        element.classList.add("ng-invalid");
      });
    }
  }

  //when upload file
  onFileUpload(event) {
    if (!this.data['file'])
      this.data['file'] = [];

    let j = this.data['file'].length;
    for (let i = 0; i < event.files.length; i++) {
      let tempFile: File = event.files[i];
      let myReader: FileReader = new FileReader();

      if (tempFile) {
        myReader.readAsDataURL(tempFile);

        //save the file name
        this.data['file'].push({
          fileName: tempFile.name,
          fileType: tempFile.type.split("/")[1],
          fileData: ""
        });

      }

      myReader.onloadend = (e) => {
        this.data['file'][j].fileData = <any>myReader.result;
        j++
        return;
      }
    }
  }

  //
  onRemoveFile(event) {
    let tempFile: File = event.file;
    if (tempFile) {

      for (let j = 0; j < this.data['file'].length; j++) {
        if (this.data['file'][j].fileName == tempFile.name)
          this.data['file'].splice(j, 1);
      }
    }
  }

  onSubmit() {
    this.sending = true;

    //send email
    return new Promise((resolve, reject) => {
      this.sendEmailService.sendEmail(this.data).subscribe(
        (response: any) => {

          if (response == true) {
            this.responseMessage['response'] = response;
            this.responseMessage['text'] = "תודה רבה שפנית אלינו הודעתך נמסרה ונטפל בך במהרה!";
          }
          else {
            this.responseMessage['response'] = false;
            this.responseMessage['text'] = "אנו מצטערים אבל משו השתבש בבשקה נסה שוב!";
          }

          this.sending = false;

          //close modal
          this.closeModal(this.responseMessage);
        },
        (error: HttpErrorResponse) => {
          reject(error);
        })
    })


  }


  closeModal(response?) {
    $('#myModal').modal('hide');

    if (response)
      this.onCloseModal.emit(response);
    else
      this.onCloseModal.emit();
  }
}
