import { Component, OnInit } from '@angular/core';
import { MessageService } from 'primeng/api';
declare var $: any;

@Component({
  selector: 'app-avi-amsalem-helping',
  templateUrl: './avi-amsalem-helping.component.html',
  styleUrls: ['./avi-amsalem-helping.component.css']
})
export class AviAmsalemHelpingComponent implements OnInit {

  public dataForModal: any;
  public showModal: boolean = false;
  public computer: boolean = true;


  constructor(private messageService: MessageService) { }

  ngOnInit() {
    let width = $(window).width();
    if ( width > 768) {
      this.computer = true;
    }
    else{
      this.computer = false;
    }
  }

  //open modle of details
  openModal(fieldOf) {
    this.dataForModal = fieldOf;
    this.showModal = true;
  }

  //
  onCloseModal(event) {
    this.showModal = false;

    if (event) {
      if (event.response) {
        alert(event.text);
        this.messageService.add({ severity: 'success', summary: 'Service Message', detail: event.text });
      }
      else {
        alert(event.text);
        this.messageService.add({ severity: 'error', summary: 'Error Message', detail: event.text });
      }
    }
  }

}
